body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  background: #f0f0f7;
  height: 1025px;
}

.fc-right .fc-prev-button, .fc-right .fc-next-button{
  background-color: #b1d583;
  background-image: none;
}

body .fc {
  background: white;
  padding: 0 20px;
  margin: 0 20px 190px 75px;
}

.body .fc::before {
  content: 'dfd';
  color: black;
}

.MuiDialogContent-root:first-child {
  width: auto !important;
}

.dialog__button {
  box-sizing: content-box;
  color: #d6d6d6;
  background: white;
  position: absolute;
  border: 1px solid #d6d6d6;
  border-radius: 50%;
  height: 20px;
  right: 7px;
  top: 6px;
  font-family: 'Source Sans Pro', sans-serif;
}

.MuiDialogActions-root {
  justify-content: space-between !important;
}

#button__Save {
  color: #6A6996 !important;
}

.MuiButton-root {
  text-transform: none !important;
}

.MuiButton-label {
  color: #ff5f5f;
  font-family: 'Source Sans Pro', sans-serif;
}

.MuiDialogActions-spacing > * + * {
  margin-right: 9px;
  margin-left: 8px;
}

.button__Save {
  border: none;
  background: none;
  color: #6a6996;
  font-size: 15px;
  cursor: pointer;
  font-family: 'Source Sans Pro', sans-serif;
}

.MuiDialogContent-root:first-child {
  width: 201px;
}


.body {
    margin: 0;
    padding: 0;
  }
  .header {
    margin: 0 auto;
    padding: 0 auto;
    min-width: 1750px;
    align-items: center;
    height: 70px;
    background: #fff;
    grid-template-columns: 15% 23% 4% 3% 0% 5% 3% 5% 5%;
    grid-template-rows: 1fr;
    display: grid;
  }

  .hide {
    display: block;
  }
  
  .header__search {
    width: 16px;
    cursor: pointer;
  }

  .header__serch__container {
    display: flex;
    justify-content: center;
    text-align: center;
    padding-left: 10px;
  }

  .header__search__title {
    text-transform: uppercase;
    background: #43425D;
    font-weight: bold;
    color: #fff;
    width: 260px;
    height: 70px;
    padding-top: 26px;
    box-sizing: border-box;
  }
  
  .n__header__input {
    border: none;
    width: 300px;
    padding: 0px 11px;
    cursor: pointer;
  }
  
  .header__search__input {
    padding: 0px 11px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
  
  .header__avatar {
    width: 38px;
    height: 38px;
    cursor: pointer;
  }
  
  .header__planet {
    width: 16px;
    height: 16px;
    opacity: 1;
    cursor: pointer;
  }
  
  .header__name {
    display: inline-grid;
    margin: 0;
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .header {
      max-width: 720px;
      grid-template-columns: 15% 3% 5% 5% 5%;
      grid-template-columns: 15% 15% 7% 2% 0%;
    }
    .hide {
      display: none;
    }
    .App {
      max-width: 767px; 
    }
  }
  
  @media (max-width: 414px) {
    .App {
      max-width: 414px
    }
  }
.fc-toolbar h2 {
